import CSS from "csstype";

// company types
export enum CompanyIds {
  NM = "nm",
  Evernote = "evern",
  GCIO = "gcio",
  Carallel = "cara",
  XpertDox = "xpdx",
  StandardFurniture = "stnfn",
  SVM = "svm",
  PlusSum = "ps",
  AFM = "afm",
  MIT = "mit",
}

export interface Employer {
  id: CompanyIds;
  company: string;
  logo: string;
  cityState: string;
  startDate: string;
  endDate: string;
  title: string;
  description: string;
  bullets: string[];
  logoClass?: string;
}

export interface Education {
  title: string;
  issuer: string;
  type: string;
  startDate: string;
  endDate: string;
  url?: string;
}

export interface ResumeData {
  jobs: Employer[];
  education: Education[];
}

export type CompanyImage = {
  name: string;
  img: string;
  iconStyle?: CSS.Properties;
  fullLogo?: string;
};

// skill types
export enum SkillIds {
  React = "react",
  ReactNative = "reactnative",
  NodeJS = "nodejs",
  Redux = "redux",
  Typescript = "typescript",
  ThreeJS = "threejs",
  Sass = "sass",
  Angular = "angular",
  HTML = "html",
  CSS = "css",
  JS = "js",
  JQuery = "jquery",
  PHP = "php",
  MongoDB = "mongodb",
  Webpack = "webpack",
  Wordpress = "wordpress",
  Illustrator = "illustrator",
  AdobeXD = "adobexd",
  Photoshop = "photoshop",
}

export enum SkillTags {
  Frameworks = "fw",
  Frontend = "fe",
  Backend = "be",
  Star = "star",
  Heart = "hrt",
  Language = "lang",
  Libraries = "lib",
  CMS = "cms",
}

export enum SkillSorts {
  BestToWorst = "besttoworst",
  WorstToBest = "worsttobest",
  AtoZ = "atoz",
}

export enum SkillLevels {
  Beginner = "Beginner",
  Familiar = "Familiar",
  Intermediate = "Intermediate",
  Skilled = "Skilled",
  Mastered = "Mastered",
}

export type Skill = {
  /* matches skills-data.json in build */ title: string;
  id: string;
  level: number;
  tags: SkillTags[];
  companies: CompanyIds[];
  description: string;
  accolades: string[];
  yearsOfExp: number;
};

export interface SkillContextValue {
  skillDescription?: SkillIds;
  skillDescriptionVisibility?: boolean;
  filters: SkillTags[];
  query: string;
  sort: SkillSorts;
  skillsList?: Skill[] | null;
  origin?: Skill[] | null;
}

export type SkillImage = {
  name: string;
  img: string;
};
